body{
    background-color:  #E7E9EB;
   }
      
   /* Switch Panel  */
   
   .brand-logo {
       color: rgb(255, 255, 255);
       font-size: 11px;
       position: absolute;
       z-index: 10;
       top: 85%;
       left: 85%;
   }
   
   .softkilQ-base-panel {
       position: relative;
       background-color: #18191a;
       border: 2px solid #ffc107;
       border-radius: 10px;
       height: 180px;
   }
   
   .softkilQ-panel-section {
       height: 100%;
       float: left;
       position: relative;
   }
   
   .softkilQ-panel-4 {
       width: 25%;
   }
   
   .softkilQ-panel-3 {
       width: 33.33%;
   }
   
   .softkilQ-panel-2 {
       width: 50%;
   }
   
   .softkilQ-panel-1 {
       width: 100%;
   }
   
   .softkilQ-panel-switch {
       width: 40px;
       height: 50px;
       border: 4px solid white;
       margin-top: 30%;
   }
   
   .softkilQ-panel-switch-left {
       float: left;
       margin-left: 20%;
   }
   
   .softkilQ-panel-switch-center {
       float: left;
       margin-left: 40%;
   }
   
   
   .softkilQ-panel-switch-right {
       float: right;
       margin-right: 20%;
   }
   
   .softkilQ-panel-socket-left {
       width: 76%;
       height: 125px;
       border: 4px solid white;
       margin-top: 18%;
       margin-left: 20%;
   }
   
   .softkilQ-panel-socket-right {
       width: 76%;
       height: 125px;
       border: 4px solid white;
       margin-top: 18%;
       margin-right: 20%;
   }
   
   
   
   .container-fluid {
       overflow: hidden;
       margin-top: 250px;
       background: #262626;
       color: #627482 !important;
       margin-bottom: 0;
       padding-bottom: 0
   }
   
   
   
   
   
   small {
       font-size: calc(12px + (15 - 12) * ((100vw - 360px) / (1600 - 360))) !important
   }
   
   .bold-text {
       color: #989c9e !important
   }
   
   .mt-55 {
       margin-top: calc(50px + (60 - 50) * ((100vw - 360px) / (1600 - 360))) !important
   }
   
   h3 {
       font-size: calc(34px + (40 - 34) * ((100vw - 360px) / (1600 - 360))) !important
   }
   
   .social {
       font-size: 21px !important
   }
   
   .rights {
       font-size: calc(10px + (12 - 10) * ((100vw - 360px) / (1600 - 360))) !important
   }
   
   
   
   /* footer */
   .payment-icons {
       -webkit-user-select: none;
       -moz-user-select: none;
       -ms-user-select: none;
       user-select: none;
       cursor: default;
       margin-bottom: 20px;
       text-align: left;
   }
   
   
   .payment-icons li {
       cursor: default;
       margin: 0 4px 0;
   }
   
   
   .inline-list li {
       display: inline-block;
       margin-bottom: 0;
       vertical-align: middle;
   }
   
   
   
   /* register form */
   
   .divider-text {
       position: relative;
       text-align: center;
       margin-top: 15px;
       margin-bottom: 15px;
   }
   .divider-text span {
       padding: 7px;
       font-size: 12px;
       position: relative;   
       z-index: 2;
   }
   .divider-text:after {
       content: "";
       position: absolute;
       width: 100%;
       border-bottom: 1px solid #ddd;
       top: 55%;
       left: 0;
       z-index: 1;
   }
   
   .btn-facebook {
       background-color: #405D9D;
       color: #fff;
   }
   .btn-twitter {
       background-color: #42AEEC;
       color: #fff;
   }