/* footer */
.payment-icons {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    cursor: default;
    margin-bottom: 20px;
    text-align: left;
}


.payment-icons li {
    cursor: default;
    margin: 0 4px 0;
}


.inline-list li {
    display: inline-block;
    margin-bottom: 0;
    vertical-align: middle;
}
